* {
    padding: 0;
    margin: 0;
    font-family: Poppins;
}

.hero {
    height: 100vh;
    width: 100%;
    background-image: url(bgokta.jpg);
    background-size: cover;
    background-position: center;
}

nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 15px;
    padding-left: 5%;
    padding-right: 5%;
}

nav ul li {
    list-style-type: none;
    display: inline-block;
    padding: 10px 25px;
    padding-left: 2px;
    font-weight: bold;
}

nav ul li a {
    color: black;
    text-decoration: none;
    font-weight: regular;
    text-transform: capitalize;
}

nav ul li a:hover {
    color: #1D6FB5;
    transition: all .4s ease;

}

.btn {
    background-color: #1D6FB5;
    color: white;
    text-decoration: none;
    border: 1px solid transparent;
    padding: 10px 25px;
    border-radius: 5px;
    transition: transform .4s;
    font-weight: bold;
}

.btn:hover {
    transform: scale(1.2);
    background-color: white;
    color: #1D6FB5;
}
.content {
    position: absolute;
    top: 55%;
    right: 20%;
    transform: translateY(-60%);
    font-weight: regular;
}

span {
    color: goldenrod;
    font-family: Poppins;
}

h1 {
    color: black;
    margin: 20px 0px 20px;
    font-size: 75px;
    font-family: Poppins;
}

h1:hover {
    background: -webkit-linear-gradient(#1D6FB5, #253B76);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    cursor: default;
}

h3 {
    color: black;
    margin-bottom: 50px;
    font-size: 25px;
    font-family: Poppins;
    cursor: default;
}

h4 {
    color: black;
    font-size: 20px;
    cursor: default;
    letter-spacing: 2px;
    font-family: Poppins;
}

.newslatter form {
    width: 380px;
    max-width: 100%;
    position: relative;
}

.newslatter:hover {
    background-color: #262729;
    color: #E5E5E5;
}

.newslatter form input:first-child {
    display: inline-block;
    width: 100%;
    padding: 14px 130px 14px 15px;
    border: 2px solid black;
    outline: none;
    border-radius: 5px;
}

.newslatter form input:last-child {
    position: absolute;
    display: inline-block;
    outline: none;
    padding: 10px 30px;
    border-radius: 5px;
    background-color: black;
    color: white;
    top: 4px;
    right: -145px;
}

.about {
    width: 100%;
    padding: 100px 0px;
    background-color: #253B76;
}

.about img {
    height: auto;
    width: 430px;
    border-radius: 10px;
}

.about-text {
    width: 550px;
}

.main {
    width: 1130px;
    max-width: 95%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.about-text h2 {
    color: #E5E5E5;
    font-size: 75px;
    text-transform: capitalize;
    font-family: Poppins;
    margin-bottom: 20px;
}

.about-text h5 {
    color: #E5E5E5;
    letter-spacing: 2px;
    font-size: 22px;
    margin-bottom: 25px;
    text-transform: capitalize;
    font-family: Poppins;
}

.about-text p {
    color: #E5E5E5;
    letter-spacing: 1px;
    line-height: 28px;
    font-size: 18px;
    margin-bottom: 45px;
}

.btn2 {
    background-color: #E5E5E5;
    color: #1D6FB5;
    text-decoration: none;
    border: 2px solid transparent;
    font-weight: bold;
    padding: 13px 30px;
    border-radius: 5px;
}

.btn2:hover {
    background-color: transparent;
    border: 2px solid #E5E5E5;
    cursor: pointer;
    color: white;
}

.service {
    background-color: #E5E5E5;
    width: 100%;
    padding: 20px 0px;
}

.tittle h2 {
    color: #262729;
    font-weight: bold;
    font-size: 55px;
    width: 1130px;
    margin: 15px auto;
    text-align: center;
}

.box {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 400px;
    padding-bottom: 20px;
}

.card {
    height: 365px;
    width: 335px;
    padding: 20px 35px;
    background: #253B76;
    border-radius: 10px;
    margin: 15px;
    position: relative;
    overflow: hidden;
    text-align: center;
}

.card i {
    font-size: 50px;
    display: block;
    text-align: center;
    margin: 25px 0px;
    color: goldenrod;
}

h5 {
    color: #E5E5E5;
    font-size: 23px;
    margin-bottom: 15px;
    font-weight: normal;
}

.pra p {
    color: #E5E5E5;
    font-size: 16px;
    line-height: 27px;
    margin-bottom: 25px;
}

.card .button {
    background-color: #E5E5E5;
    color: #262729;
    text-decoration: none;
    border: 2px solid transparent;
    font-weight: normal;
    padding: 9px 22px;
    border-radius: 5px;
    transition: .4s;
}

.card .button:hover {
    background-color: transparent;
    border: 2px solid #E5E5E5;
    cursor: pointer;
    color: #E5E5E5;
}

footer {
    position: relative;
    width: 100%;
    height: 320px;
    background: #E5E5E5;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

footer p:nth-child(1) {
    font-size: 30px;
    font-weight: bold;
    color: #262729;
    margin-bottom: 20px;
}

footer p:nth-child(2) {
    color: #262729;
    font-size: 17px;
    width: 500px;
    text-align: center;
    line-height: 26px;
    text-transform: capitalize;
}

.social {
    display: flex;
}

.social a {
    width: 45px;
    height: 45px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #262729;
    border-radius: 50%;
    margin: 22px 10px;
    color: goldenrod;
    text-decoration: none;
    font-size: 20px;
}

.social a:hover {
    transform: scale(1.3);
    transition: .3s;
}

.end {
    position: absolute;
    color: #262729;
    bottom: 20px;
}


.trackingbro{
    align-items: center;
    padding-left: 45%;
    padding-top: 50px;
    padding-bottom: 50px;
}
.step {
    position: relative;
    min-height: 1em;
    color: gray;
}

.title {
    line-height: 1.5em;
    font-weight: bold;
}

.caption {
    font-size: 0.8em;
}

.step+.step {
    margin-top: 1.5em
}

.step>div:first-child {
    position: static;
    height: 0;
}

.step>div:not(:first-child) {
    margin-left: 1.5em;
    padding-left: 1em;
}

/* Circle */
.circle {
    background: gray;
    position: relative;
    width: 1.5em;
    height: 1.5em;
    line-height: 1.5em;
    border-radius: 100%;
    color: #fff;
    text-align: center;
    box-shadow: 0 0 0 3px #fff;
}

/* Vertical Line */
.circle:after {
    content: ' ';
    position: absolute;
    justify-content: center;
    display: block;
    top: 1px;
    right: 50%;
    bottom: 1px;
    left: 50%;
    height: 100%;
    width: 1px;
    transform: scale(1, 2);
    transform-origin: 50% -100%;
    background-color: rgba(0, 0, 0, 0.25);

}

.step:last-child .circle:after {
    display: none
}

.step.step-active {
    color: #4285f4
}

.step.step-active .circle {
    background-color: #4285f4;
}

.icon{
    font-size: 50px;
    margin: 25px 0px;
    color: goldenrod;
}

.edu h2{
    font-size: 55px;
    text-align: center;
    padding-top: 50px;
}

.edu p{
    font-size: 25px;
    font-weight: lighter;
    text-align: center;
    color: #1D6FB5;
    padding-top: 30px;
}

